html,
body {
  font-family: 'Open Sans', sans-serif;
  scroll-behavior: smooth;
}
body {
  padding-top: 90px;
}
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(255, 255, 255, 0);
  -webkit-box-shadow: inset 0 0 6px rgba(255, 255, 255, 0);
  background-color: #fff;
}
::-webkit-scrollbar {
  width: 6px;
  background-color: #fff;
}
::-webkit-scrollbar-thumb {
  border-radius: 50px;
  box-shadow: inset 0 0 6px rgba(255, 255, 255, 0);
  -webkit-box-shadow: inset 0 0 6px rgba(255, 255, 255, 0);
  background-color: #ED1D24;
}
a {
  text-decoration: none;
}
ul {
  padding: 0;
  list-style-type: none;
}
.title {
  margin-bottom: 35px;
}
.title span {
  font-size: 24px;
  color: #808080;
}
.title h3 {
  font-weight: bold;
  color: #ED1D24;
  font-size: 34px;
}
footer {
  background-image: url('../../img/bg-footer.png');
  max-height: 115px;
  padding: 30px 0;
}
footer .content-left {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-end;
}
footer .content-left .address {
  flex: 0 0 50%;
  max-width: 50%;
}
footer .content-left .address p {
  font-size: 14px;
  color: #808080;
}
footer .content-left .logo {
  flex: 0 0 50%;
  max-width: 50%;
  align-items: flex-end;
}
footer .content-right .social-network {
  display: flex;
  justify-content: flex-end;
}
footer .content-right .social-network .item {
  padding-right: 35px;
}
footer .content-right .social-network .item:last-child {
  padding-right: 0;
}
footer .content-right .list-contact {
  display: flex;
  justify-content: space-between;
}
footer .content-right .list-contact .item {
  margin-top: 23px;
}
footer .content-right .list-contact .item p {
  font-size: 14px;
  color: #808080;
}
header {
  position: fixed;
  top: 0;
  width: 100%;
  padding: 15px 0;
  background: #191919;
  z-index: 100;
}
header.active {
  box-shadow: 0 0 7px rgba(0, 0, 0, 0.1);
}
header .mobile-toggle {
  display: none;
  position: absolute;
  right: 35px;
  bottom: 16px;
  width: 43px;
  height: 38px;
  cursor: pointer;
  z-index: 40;
  transition: all 500ms;
}
header .mobile-toggle .button_toggle {
  position: relative;
}
header .mobile-toggle .button_toggle .line {
  position: absolute;
  top: 0;
  display: block;
  content: '';
  width: 23px;
  right: 15px;
  height: 3px;
  background: #ED1D24;
  transition: all 0.25s ease-in-out;
}
header .mobile-toggle .button_toggle .line:nth-child(2) {
  top: 8px;
  width: 23px;
}
header .mobile-toggle .button_toggle .line:nth-child(3) {
  top: 16px;
}
header .mobile-toggle .button_toggle .line-close-1 {
  -webkit-transform: rotate(45deg) translate(12px, 0);
  -ms-transform: rotate(45deg) translate(12px, 0);
  transform: rotate(45deg) translate(12px, 0);
}
header .mobile-toggle .button_toggle .line-close-3 {
  -webkit-transform: rotate(-45deg) translate(10px, 0);
  -ms-transform: rotate(-45deg) translate(10px, 0);
  transform: rotate(-45deg) translate(10px, 0);
}
header .mobile-toggle .button_toggle .line_hidden {
  opacity: 0;
}
header .list-menu {
  height: 100%;
  display: flex;
  margin-left: auto;
}
header .list-menu .item-menu {
  padding: 0 5px;
  flex-basis: 0;
  flex-grow: 1;
  width: 228px;
}
header .list-menu .item-menu a {
  display: block;
  text-align: center;
  text-transform: uppercase;
  font-weight: bold;
  line-height: 60px;
  color: #FFFFFF;
  font-size: 14px;
  background: #353535;
  width: 100%;
  transition: 0.3s all ease-in-out;
}
header .list-menu .item-menu:hover a {
  background: #ED1D24;
}
@media (max-width: 1024px) {
  .container {
    padding: 0;
  }
  header .mobile-toggle {
    display: block;
  }
  header .logo {
    padding-left: 35px;
  }
  header .list-menu {
    background: #353535;
    display: block;
    position: fixed;
    margin-left: unset;
    width: 100%;
    height: 100vh;
    top: 90px;
    left: -100%;
    transition: 0.3s all ease-in-out;
  }
  header .list-menu.active {
    left: 0;
  }
  header .list-menu .item-menu {
    padding: 0;
    width: 100%;
  }
  header .list-menu .item-menu a {
    padding-left: 35px;
    text-align: left;
  }
}
