.pagination {
  width: 100%;
  margin-top: 10px;
}
.pagination .list-number {
  display: flex;
  justify-content: center;
  position: relative;
}
.pagination .list-number:after {
  content: '';
  display: block;
  position: absolute;
  width: 5px;
  height: 10px;
  top: 50%;
  transform: translateY(-50%);
  right: 390px;
  background-image: url('../img/icons/pagination_next.png');
}
.pagination .list-number:before {
  content: '';
  display: block;
  position: absolute;
  width: 5px;
  height: 10px;
  top: 50%;
  transform: translateY(-50%);
  left: 390px;
  background-image: url('../img/icons/pagination_prev.png');
}
