.list-item {
    padding   : 100px 0 40px;
    background: #F2F2F2;

    .title {
        text-align: center;
        color     : #000000;
        font-size : 36px;
    }

    .list-post {
        .item {
            padding-bottom: 40px;

            figure {
                padding   : 10px 10px 0;
                background: #FFFFFF;
                box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16);

                .box-img {
                    display: flex;

                    img {
                        width : 100%;
                        height: auto;
                    }
                }

                figcaption {
                    padding: 30px 40px;
                    display: flex;

                    .content {
                        justify-content: flex-start;
                        width          : 70%;

                        h5 {
                            font-size    : 24px;
                            font-weight  : 800;
                            color        : #353535;
                            margin-bottom: 15px;
                        }

                        span {
                            font-size: 16px;
                            color    : #AFAFAF;
                        }
                    }

                    .voted {
                        width          : 30%;
                        justify-content: flex-end;
                        position       : relative;

                        span {
                            position : absolute;
                            color    : #000000;
                            font-size: 16px;
                            right    : 0px;
                            top      : 9px;
                        }

                        .icon-vote {
                            position: absolute;
                            cursor  : pointer;
                            bottom  : 5px;
                            right   : 0px;
                            width   : 20px;
                            height  : 20px;

                            .heart-full,
                            .heart-nofull {
                                background-position: center;
                                background-size    : 100%;
                                background-repeat  : no-repeat;
                            }

                            .heart-full {
                                background-image: url(../img/icons/heart-full.jpg.png);
                                position        : absolute;
                                bottom          : 0px;
                                left            : 0;
                                width           : 0;
                                height          : 100%;

                                &.active {
                                    width: 100%;
                                }
                            }

                            .heart-nofull {
                                background-image: url(../img/icons/heart-nofull.png);
                                width           : 100%;
                                height          : 100%;
                            }
                        }
                    }
                }
            }
        }
    }
}